<template>
    <ul id="switch-names"><li id="blue" v-bind:style="{fontWeight:nameWeight1}" >Web design</li> <li id="aqua" v-bind:style="{fontWeight:nameWeight2}">Graphic design</li> <li id="green" v-bind:style="{fontWeight:nameWeight3}">Other stuff</li></ul>
</template>


<script>
import { EventBus } from '../main.js'
export default {


data: function (){
    return{
        currentPage: "page1",
        nameWeight1: "600",
        nameWeight2: '',
        nameWeight3: '',

    }
},
watch:{
    currentPage: function () {
      if (this.currentPage === "page1"){
        this.nameWeight1="600" 
      } else {
          this.nameWeight1=""
      }
       if (this.currentPage === "page2"){
        this.nameWeight2="600" 
      } else {
          this.nameWeight2=""
      }
       if (this.currentPage === "page3"){
        this.nameWeight3="600" 
      } else {
          this.nameWeight3=""
      }

    }
  },

mounted(){
EventBus.$on('PageChange',data =>{
this.currentPage = data;
  });



},
   
    
}
</script>




<style scoped>


#switch-names{
    list-style:none;
    width: 90%px;
    margin: auto;

    text-align: center;
   margin-left: -50px;
   margin-bottom: -60px;
   margin-top: 20px;

}

#switch-names li {
    display: inline-block;
     margin: 5px;
     font-size: .8em;
     font-weight: 300;
     transition: .3s;
}


#blue{
color: rgb(33, 112, 180);
}

#aqua{
color: rgb(33, 144, 162);
}

#green{
color: rgb(33, 177, 143);
}

</style>

