<template>
    <div>
        <div class="switchHolder">
            
            <form>
            <input class="switch-button" checked id="switchOne" name="switch" type="radio" value="yes" v-on:click="paged='page1'" />
            <label class="switch-lable" for="switchOne" v-bind:style="{backgroundColor:clickedColor1}" v-on:click="switchClick1" ></label>

            <input class="switch-button" id="switchTwo" name="switch" type="radio" value="maybe" v-on:click="paged ='page2' " />
            <label  class="switch-lable" for="switchTwo" v-bind:style="{backgroundColor:clickedColor2}" v-on:click="switchClick2" ></label>

            <input class="switch-button" id="switchThree" name="switch" type="radio" value="no" v-on:click="paged ='page3'" />
            <label class="switch-lable" for="switchThree" v-bind:style="{backgroundColor:clickedColor3}" v-on:click="switchClick3" ></label>
            
            </form>
                   
            </div>
            
        
    </div>
</template>

<script>
import { EventBus } from '../main.js'

export default {
data: function () {
    return {
        paged: '',
        clickedColor1:'white',
        clickedColor2:'',
        clickedColor3:'',
        
    }
},
watch:{
    paged: function() {
        this.$emit('PageChange', this.paged)
        EventBus.$emit('PageChange', this.paged)
    }
},

methods:{
 switchClick1: function (){
     this.clickedColor1='white'
     this.clickedColor2=''
     this.clickedColor3=''
 },
  switchClick2: function (){
     this.clickedColor1=''
     this.clickedColor2='white'
     this.clickedColor3=''
 },
  switchClick3: function (){
     this.clickedColor1=''
     this.clickedColor2=''
     this.clickedColor3='white'
 }
}
    
}
</script>

<style>


.switchHolder{
    text-align: center;
    width: 200px;
    margin: auto;
    background-image: linear-gradient(to right, rgb(33, 112, 180) , rgb(33, 144, 162) , rgb(33, 177, 143) );
    position: relative;
    bottom: -58px;
    border-radius: 360px;
    z-index: 1;
    color:white;    
    height: 25px;
    display: block;
    text-align: center;
    margin-top: -15px;
    font-size: .7em;
    padding-top: 7px;
    box-shadow: 0px 1px 3px 0px grey;
}

.switch-button{
    display:none;
}

.switch-lable{
display: inline-block;
margin-left: 15px;
margin-right: 15px;
height: 13px;
width: 13px;
background-color: rgba(255, 255, 255, 0.26);
border-radius: 360px;
cursor: pointer;
position: static;
transition: .5s;
border: 2px white solid;
}

.switch-lable:hover{
    background-color: rgb(255, 255, 255);
    transition: .5s;
}



</style>
