<template>
<div class="footer-holder">
    <footer class="footer" v-bind:style="{height: amheightfooter}">
        <div class="toggle-holder">
          <input id="contact-open-footer" type="checkbox" v-model="checkedfooter" v-on:click="OpenCloseFooter" />
        <label id="contact-button" for="contact-open-footer"><div class="unselectable">Contact me</div></label>  
        </div>
        <div id="contact-holder" v-bind:style="{height: contactShow, marginTop: contactMargin}">
        <my-contact></my-contact>
        </div>
    </footer>
</div>
</template>

<script>
import contact from './Contact.vue'
export default {

    components:{
        myContact: contact,
    },
    data: function (){
        return{
                checkedfooter: '',
                amheight:'',
                contactShow: '0px',
                contactMargin: '',           


        }
    },
    methods:{
        OpenCloseFooter: function () {
            if(this.checkedfooter === true){
                this.amheightfooter="40px"
                this.contactShow="0px"
                this.contactMargin="0px"
            } else {
                this.amheightfooter="95vh"
                this.contactShow="450px"
                this.contactMargin="60px"
            }
        }
            
        }
    }
    


</script>

<style>
#contact-holder{
    overflow: hidden;
    transition: .7s;

}
footer{
    height: 40px;
    width: 100%;
    margin: auto;
    background-image: linear-gradient(to right, rgba(0, 91, 170, 0.9) , rgba(0, 167, 125, 0.9) );
    position: absolute;
    bottom: 0px;
    transition: .7s;
    left: 0px;
    z-index: 2;
}

#contact-open-footer{
    display: none;
}

#contact-button{
    border: 3px #404951 solid;
    border-radius: 360px;
    width: 80px;
    height: 25px;
    display: block;
    text-align: center;
    margin-top: -17px;
    font-size: .7em;
    padding-top: 7px;
    cursor: pointer;
    margin-left: 10px;
    background-color: #404951;
    color:white;
    box-shadow: 0px -1px 3px 0px grey;
}

#contact-button:hover{
  background-color: #dadada;
    color: #404951;
    transition: .2s;
    border: 3px solid #404951;
    padding-top: 7px;
}

.toggle-holder{
    width: 100px;
    margin: auto;
    position: relative;

}

.open {
height: 40px;
}
.closed{
    height: 0px;
    
}


.transitionTest{
    transition: ease-out .5s;
}

</style>
