<template>
    <div>
        <div class="content-square-wrapper" >
            <div class="content-square grid-item"  v-bind:style="{boxShadow: focused }">
            <button id="show-modal" @click="showModal = true" v-on:mouseover="contentFocus" v-on:mouseout="unFocus" >Show Modal</button>
            
                    <modal v-if="showModal" @close="showModal = false">
                    <h3 slot="header">custom header</h3>
                    <div class="notapicture" slot="body"></div>
                    </modal>
            </div>
            <p>good discription</p>
        </div>
    </div>
</template>


<script>
import Modal from '../Modal.vue'
export default {
    components:{
        Modal: Modal,
    },
    data: function () {
        return{
          showModal: false,
          focused: '',
        }
    },
    methods:{
        contentFocus: function () {
            this.focused=" 1px 1px 3px 1px grey"
        },
        unFocus: function () {
            this.focused=""
        }
    }
}
</script>




<style scoped>
.content-square{
    background-color: rgb(132, 255, 110);
}

#show-modal{
    opacity: 0 ;
    width: 100%;
    height: 100%;
    cursor: pointer;
}

.notapicture{
    background-color: aqua;
    height: 900px;
    width: 300px;
}
.content-square{
    box-shadow:0px;
    transition: .3s;
}

</style>