<template>
    <div>
        
        <div class="about-me-holder" v-bind:style="{height: amheight, borderWidth: borderwidth , boxShadow: shadow}" >
        
        <my-about-me-grid></my-about-me-grid>

        </div>
        <div class="toggle-holder">
        <input id="about-me-open" type="checkbox" v-model="checked" v-on:click="AboutOpenClose" />
        <label id="about-me-button" for="about-me-open"> <div class="button-text unselectable">About me</div></label>
        </div>
        
        
    </div>
</template>

<script>
import AboutMeGrid from './AboutMeGrid.vue'
export default {
    components:{
        myAboutMeGrid: AboutMeGrid
    },
    data: function (){
        return{
                checked: '',
                thing: 1,
                amheight:'',
                borderwidth:'',           
                shadow: '',

        }
    },
    methods:{
        AboutOpenClose: function () {
            if(this.checked === true){
                this.amheight="0px"
                this.borderwidth='1px'
                this.shadow="none"
            } else {
                this.amheight="45vh"
                this.borderwidth='3px'
                this.shadow="1px 1px 3px 1px grey"
            }
        }
            
        }
    }
    


</script>


<style>
#about-me-open{
    display: none;
}

#about-me-button{
    border: 3px #404951 solid;
    border-radius: 360px;
    width: 80px;
    height: 25px;
    display: block;
    text-align: center;
    margin-top: -19px;
    font-size: .7em;
    padding-top: 7px;
    cursor: pointer;
    margin-left: 10px;
    background-color: #404951;
    color:white;
    box-shadow: 0px 1px 3px 0px grey;
}


#about-me-button:hover{
    background-color: #dadada;
    color: #404951;
    transition: .2s;
    border: 3px solid #404951;
    padding-top: 7px;
}

.about-me-holder{
    margin: auto;
    background-color: white;
    border: 1px #dadada solid;
    border-radius: 8px;
    transition: .7s;
    height: 0px;
    margin-top: 20px;
    overflow-x: hidden;
}

.toggle-holder{
    width: 100px;
    margin: auto;
}

.open {
height: 40px;
}
.closed{
    height: 0px;
    
}



.transitionTest{
    transition: 1s;
}

</style>

