<template>
<div class="background" :style="{'backgroundImage': changeBackground }">
  <my-header></my-header>
  <my-body></my-body>
  <p id="copyright">&copy; BenjaminRussell.Design</P>
  <my-footer></my-footer>
  </div>
</template>

<script>
 var thing = '';
import {EventBus } from './main.js'
import Header from './components/Header.vue'
import AboutMe from './components/AboutMe.vue'
import Body from './components/Body.vue'
import Footer from './components/Footer.vue'

export default {
  components: {
  myHeader: Header,
  myBody: Body,
  myFooter: Footer,

  },

     data: function (){
    return{
      currentPage: 'page1',
      changeBackground: 'url('+require('./assets/websitePictures/background1.svg')+')',
     
    }
  },

mounted(){
  EventBus.$on('PageChange',data =>{
    this.currentPage = data;
  });
},
    
   

watch:{
    currentPage: function () {
      if (this.currentPage === "page1"){
        this.changeBackground = 'url('+require('./assets/websitePictures/background1.svg')+')'
      }
      if (this.currentPage === "page2"){
        this.changeBackground = 'url('+require('./assets/websitePictures/background2.svg')+')'
      }
      if (this.currentPage === "page3"){
        this.changeBackground = 'url('+require('./assets/websitePictures/background3.svg')+')'
      }

    }
  }
  
}
</script>

<style>
.background{
  font-family: 'Montserrat', sans-serif;
  color: #404951;
    background-size: cover;
    background-repeat: no-repeat;
    overflow: hidden;
    height: 95vh;
    transition: 1.5s;
    margin-left:  -8px;
    margin-right: -8px;
}

#copyright{
  position: absolute;
  right: 10px;
  bottom: -6px;
  z-index: 5 ;
}
.unselectable {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

h1{
  font-weight: 900;
}


</style>
